import { Box, Collapse } from "@mui/material";

const ChatInfo = () => {
  return (
    <Box width="100%" maxWidth={{ md: 256, xl: 400 }}>
      <Collapse
        in={true}
        sx={{
          borderLeft: 1,
          borderLeftStyle: "solid",
          borderLeftColor: "#0000000D",
          bgcolor: "#ffffff",
          height: "100%",
          "& *": { height: "100%" },
        }}
      >
        <Box
          display="flex"
          height="100%"
          justifyContent="center"
          alignItems="center"
        >
          Info Drawer
        </Box>
      </Collapse>
    </Box>
  );
};

export default ChatInfo;
